import app from '.././app'

let correctWord;
let correctPuzzleNumber;


export  async function api() {
    let response = await fetch('https://words.dev-apis.com/word-of-the-day')        
    if (response.status === 200) {                                                  
        let data = await response.json()  
        correctWord = data.word
        correctPuzzleNumber = data.puzzleNumber                                                                                              
        console.log(`correctword:- ${correctWord} & correctPuzzleNumber:- ${correctPuzzleNumber}`)
        return correctWord
    }else {
        throw new Error("Your api network call has failed please check")
    }
}

api()

export const validateWord = async () => {
    const url = 'https://words.dev-apis.com/validate-word'
    const result = await fetch(url,{
        method: "POST",
        body:JSON.stringify({word:correctWord}),
    });

    const {validateWord} = await result.json()
    console.log("Post response  " +validateWord)
}

validateWord()

export default api