import api, { validateWord } from './apiComponent/api'

let guessWord = '';
let puzzleNumber = '';
let correctWordIs = '';
let correctPuzzleNumber = '';
let isLoading = false;
let countOfTimes = 0;
let attempts = 0;

let word;
let arr = [];


( async function apiGetWord() {
    word = await api()
    correctWordIs =await word
    console.log(correctWordIs)
})();

let inputEleAll = document.querySelectorAll('.input')   
let spinnerEl = document.getElementById('spinner')    
let wishTag = document.getElementById('wishTag')

spinnerEl.textContent=''

let enterBtn;

Array.from(inputEleAll).forEach((eachEle => {               
    
    eachEle.addEventListener('keyup',function(e) {          
       isLoading = true
       spinnerEl.textContent = 'ðŸŒ€';
        enterBtn = true
        eachEle.value = e.target.value
        
        if (correctWordIs.includes(e.target.value)){
            eachEle.setAttribute('class','match-word')
        }else {
            eachEle.setAttribute('class','input-style')
        }

        guessWord += eachEle.value                          
        if (eachEle.value !== '' && e.key !== "Enter" && e.key !== 'Backspace') {
            countOfTimes = countOfTimes + 1
        }
        if (countOfTimes == 5) {
            handleAfterAttempt()
        }
        if(e.key === "Backspace"){
            enterBtn = false
            eachEle.previousElementSibling.focus();
        }
        if (enterBtn) {
            eachEle.nextElementSibling.focus()
        }
    })
}))


function handleAfterAttempt() {  
    spinnerEl.textContent = '';   
    isLoading = false                              
    
    attempts += 1

    if (guessWord == correctWordIs) {
        spinnerEl.textContent=''
        console.log('Congratulations, Your win')
        guessWord = ''
        wishTag.textContent = '***** Congratulation you won this game *****'         
        wishTag.setAttribute('class','wish-tag')                                     
    }else{
        guessWord = '';
        wishTag.textContent = `More ${6-attempts} * Attempts only.`
        wishTag.setAttribute('class','attempts-style')
    }

    countOfTimes = 0
    inputEleAll.nextElementSibling.focus()

}